:root {
  /* Light Theme (Default) */
  --primary-color: #5c6bc0;
  --secondary-color: #7986cb;
  --accent-color: #3949ab;
  --text-color: #333;
  --bg-color: #f8f9fa;
  --light-gray: #e9ecef;
  --dark-gray: #6c757d;
  --white: #ffffff;
  --box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --transition: all 0.3s ease;
  
  /* Dark Theme Variables */
  --primary-color-dark: #7986cb;
  --secondary-color-dark: #5c6bc0;
  --accent-color-dark: #8c9eff;
  --text-color-dark: #e0e0e0;
  --bg-color-dark: #121212;
  --light-gray-dark: #333333;
  --dark-gray-dark: #b0bec5;
  --white-dark: #1f1f1f;
  --box-shadow-dark: 0 4px 6px rgba(0, 0, 0, 0.3);
}

/* Apply theme transition to all elements */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: background-color 0.3s ease, color 0.3s ease;
}

body {
  font-family: 'Roboto', 'Segoe UI', sans-serif;
  color: var(--text-color);
  background-color: var(--bg-color);
  line-height: 1.6;
}

/* Dark Theme Class */
body.dark-theme {
  --primary-color: var(--primary-color-dark);
  --secondary-color: var(--secondary-color-dark);
  --accent-color: var(--accent-color-dark);
  --text-color: var(--text-color-dark);
  --bg-color: var(--bg-color-dark);
  --light-gray: var(--light-gray-dark);
  --dark-gray: var(--dark-gray-dark);
  --white: var(--white-dark);
  --box-shadow: var(--box-shadow-dark);
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content {
  flex: 1;
  padding: 2rem 0;
}

/* Container */
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

/* Buttons */
.btn {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  background-color: var(--primary-color);
  color: var(--white);
  border: none;
  border-radius: 4px;
  text-decoration: none;
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
}

.btn:hover {
  background-color: var(--accent-color);
  transform: translateY(-2px);
  box-shadow: var(--box-shadow);
}

.btn-outlined {
  background-color: transparent;
  border: 2px solid var(--primary-color);
  color: var(--primary-color);
}

.btn-outlined:hover {
  background-color: var(--primary-color);
  color: var(--white);
}

/* Cards */
.card {
  background-color: var(--white);
  border-radius: 8px;
  box-shadow: var(--box-shadow);
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  transition: var(--transition);
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

/* Section titles */
.section-title {
  position: relative;
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 700;
  color: var(--primary-color);
}

.section-title:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -10px;
  width: 60px;
  height: 4px;
  background-color: var(--accent-color);
}

/* Responsive layout */
@media (max-width: 768px) {
  .container {
    padding: 0 1rem;
  }

  .section-title {
    font-size: 1.75rem;
  }
}
